.CollapsibleDiv:nth-of-type(odd) {
    background-color: rgba(0, 120, 212, 0.1)
}

.CollapsibleDiv:nth-of-type(even) {
    background-color: rgba(0, 120, 212, 0.2);
}

.CollapsibleDiv-HeaderDiv {
    display: flex;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
}

.CollapsibleDiv-HeaderTitle {
    font-size: x-large;
    cursor: pointer;
}

@keyframes spin {
    from {transform:rotate(90deg);}
    to {transform:rotate(270deg);}
  }